body {
  width: 100%;
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.DetailsCenter{
    display:-webkit-flex;
    display:flex;
}

.DetailsImage{
    width:45%;
}

.DetailsImage img{
    width: 100%;
}

.DetailsInsights{
    margin-left: 3%;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-justify-content: space-between;
            justify-content: space-between;
    width: 50%;
}

.InsightHeader{
    width: 30%;
}

.InsightTable{
    margin-top: 2rem;
    font-size: 1em;
    width:100%;
}

.EndStatus{
    background-color: #e80a0abd;
    border-radius: 0.5rem;
    padding: 0rem 0.3rem;
}

.OngoingStatus{
    background-color: #24d706a3;
    border-radius: 0.5rem;
    padding: 0.1rem 0.5rem;
}

.CastCardImage{
    width: 5em;
}

.CastInfoCard{
    padding: 0.4rem;
    border: 1px solid #ddd;
    margin: 0.3rem;
}

.CastCardName{
    font-size: 0.8em;
}

.TagViewer{
    margin-top: 1em;
}

.ShowDetailsModal{
    max-height: 78vh;
    overflow: auto;
}

.ShowSummary{
    margin-top: 2em;
}
.ReactModal__Content
{
    top : 50% !important;
    left : 50% !important;
    right : auto !important;
    bottom : auto !important;
    -webkit-transform : translate(-50%, -50%) !important;
            transform : translate(-50%, -50%) !important;
    min-height: 35vh;
    width: 50%;
}

.Footer{
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
}
.App {
    width: 100%;
}

.Header{
    height: 5vh;
    padding: 1em;
}

.MainContent{
    padding: 2%;
}

.horizontalList{
    display: -webkit-flex;
    display: flex;
    width: 100%;
    overflow: auto;
}

.showInfoCard{
    padding: 0.4em;
    border: 1px solid #ddd;
    margin: 0.3em;
}

.showInfoCard a{
    display: block;
    text-decoration: underline;
    cursor: pointer;
    max-width: 10em;
    margin: 0.5em 0;
}

.showInfoCard .TagViewer{
    max-width: 13.2em;
    margin-top: 0.5em;
}

.CardImage{
    width: 100%;
    height: 100%;
}

.ImageContainer{
    width: 13em;
    margin: 0 auto;
}

.CustomShowButtonPanel{
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: flex-end;
            justify-content: flex-end;
}

.Tag{
    border: 1px solid #efefef;
    background-color: #efefef;
    margin-right: 0.3em;
    padding: 0.1em;
    font-size: 0.9em;
    border-radius: 0.2em;
}

.SearchInput{
    border-radius: 1em;
    margin-left: 0.2em;
    height: 4vh;
    width: 25%;
}

.ListHeader{
    display: -webkit-flex;
    display: flex;
}

.ListHeader h2{
    margin-top: 20px;
    margin-bottom: 20px;
}

.ButtonPanel{
    margin: auto 0.8em;
}

.AddCustomShowBtn{
    font-size: 1.3em;
}

.CustomShowButtonPanel button{
    padding: 0 0.3em !important;
}
